.App {
    text-align: center;
}

.App-logo {
    animation: App-logo-spin infinite 20s linear;
    height: 40vmin;
    pointer-events: none;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.page-container {
    background-image: url('https://i.imgur.com/IUySTVJ.jpg');
    background-size: cover;
    background-color: #81878a;
}

.content-container {
    height: 100%;
    width: 100%;
    background: rgba(62, 54, 54, 0.4);
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.inner {
    height: 700px;
    background: rgba(0, 0, 0, .4);
}

.icon {
    margin-top: 30px;
    display: flex;
    display: inline-block;
    color: inherit;
    font-style: normal;
    line-height: 0;
    text-align: center;
    text-transform: none;
    vertical-align: -.125em;
}

.icon a {
    font-size: 100px;
}

.icon i {
    font-size: 100px;
}
